
import {defineComponent} from 'vue';
import {useRoute} from "vue-router";

export default defineComponent({
  name: "ComponentList",

  setup() {
    /** @type {RouteRecordRaw[{}]} */
    const routes = useRoute().matched[0].children;
    return {routes: routes.map(({path, meta}) => ({path, title: meta && meta.title}))};
  }

});
